.app {
  text-align: center;
}

.app__logo {
  -webkit-animation: app__logo--spin infinite 20s linear;
          animation: app__logo--spin infinite 20s linear;
  height: 80px;
}

.app__header {
  background-color: #222;
  padding: 20px;
  margin-bottom: 20px;
  color: white;
  clear: both;
}

.app__intro {
  font-size: large;
}

@-webkit-keyframes app__logo--spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes app__logo--spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}




